<template>
  <div id="app">
    <aheader></aheader>
     <router-view></router-view>
  <div class="container ">
        <div class="row">
            <div class="col s2">  
                <h2>Random Number Game</h2>
              
                    <h3>Enter Four Numbers of your Choice</h3>
                    <input type="text" placeholder="" ref="number" > 
                    <div >{{errorbox}}</div>
                </div> 
        </div>
        <hr>
        <div class="row" >
            <h2>Auto-Generated Numbers</h2>
            <span></span>  <span></span> <span></span> <span></span>
            <div  class="divider" ></div>
            <button  v-on:click="generateRandomNum" id="btn">Click to Generate </button>
            <h4>Game Instruction </h4>
                <p>Enter 4 Numbers of your choice and then click the button.</p>
            <p>If any 3 of your numbers matches the auto-generated numbers, then you have won.</p>
            <p>If any 2 of your numbers matches the auto-generated numbers, that's a draw</p>
            
        </div>
