<script>

import Header from "./components/Header.vue"

export default {
  name: 'App',
  components:{
    aheader:Header
  },
  data(){
    return{
      
      errorbox: ""
    }
  },
 
  methods: {
generateRandomNum: function(number){

    number = this.$refs.number.value;
    if(number ==""){
        this.errorbox = "Enter a Valid Number"
        
        setTimeout((function(){ location.reload()}
        ), 1000)
    }else{


    let randomNumber = Math.fround(Math.random()*8)
    
 
    let fourDigits = randomNumber.toFixed(3)
    
 
    let regex = /\./g
    let FinalNumber = fourDigits.replace(regex, '')
    
    
 
 let showOutput = document.getElementsByTagName('span')
 
 showOutput[0].style.backgroundColor = 'black'

showOutput[1].style.backgroundColor = 'green'
showOutput[2].style.backgroundColor = 'blue'
showOutput[3].style.backgroundColor = 'red'


 showOutput[0].innerHTML = FinalNumber[0]
 showOutput[1].innerHTML = FinalNumber[1]
 showOutput[2].innerHTML = FinalNumber[2]
 showOutput[3].innerHTML = FinalNumber[3]
}
 
}

 


  }
}

  
</script>

